.dialogsWrapper {
  display: grid;
  grid-template-columns: 4fr 8fr;
  padding: 20px
}

.dialogs {
  display: flex;
  flex-flow: column;
  align-items: flex-start;
}

.dialog {
  margin: 4px;
}

.dialog a {
  text-decoration: none;
  padding: 3px;
}

.dialog .active {
  background-color: #eee;
}